<template>
  <div class="input-container flex-start">
    <p class="label-style">{{ label }}</p>
    <el-input
      class="el-input-style"
      :placeholder="placeholder"
      :value="value"
      :show-password="password"
      @input="onInput"
    >
    </el-input>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: "标题",
    },
    value: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    password: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    onInput(val) {
      this.$emit("update:value", val);
    },
  },
};
</script>

<style lang="scss" scoped>
.input-container {
  padding: px(0) px(12);
  background: #ffffff;
  border-radius: px(8);
  border: px(1) solid #eeeeee;
  .label-style {
    width: px(64);
    font-size: px(14);
    font-weight: bold;
    color: #333333;
  }
  .el-input-style {
    ::v-deep {
      .el-input__inner {
        padding-right: 0;
        border: none;
        overflow: hidden;
      }
    }
  }
}
</style>
